import React, { useState, useRef } from 'react'
import { Switch, Route, Link, useHistory } from 'react-router-dom'
import { connect } from 'react-redux'
import Filter from './Filter'
import Blog from './Blog'
import BlogForm from './BlogForm'
import Togglable from './Togglable'
import { createBlog, likeBlog, deleteBlog } from '../reducers/blogReducer'
import { setMessage } from '../reducers/messageReducer'

const Blogs = (props) => {

  const history = useHistory()
  const [ fromCreate, setFromCreate ] = useState(true)
  const [ visible, setVisible ] = useState(true)
  // const hideWhenForm = { display: visible ? '' : 'none' }
  const blogFormRef = useRef()

  const blogForm = () => (
    <Togglable buttonLabel='create new' ref={blogFormRef}>
      <BlogForm
        createBlog={createBlog}
        user={props.user}
        setFromCreate={setFromCreate}
        onClick={() => {
          setVisible(false)
          setFromCreate(false)
          console.log('visible: ',visible === true)
          history.push('/')
        }} />
    </Togglable>
  )

  return (
    <>
      <div style={{ display: visible ? '' : 'none' }}>
        <Filter />
        <h2>Blogs</h2>
        <div>
          {/* <Link to='/create'
            onClick={() => {
              setFromCreate(true)
              history.push('/blogs')
            }}>add new blog
          </Link> */}
          {props.user !== null && fromCreate === true
            ? blogForm()
            : null
          }
        </div>
        <div>
          <ul>
            {props.blogs.map(blog =>
              <li id='boxed' className='list-group-item' key={blog.id}>
                <Link
                  to={`/blogs/${blog.id}`}
                  onClick={() => {
                    setVisible(false)
                    setFromCreate(false)
                    history.push('/blogs')
                  }}
                >
                  {blog.title}
                </Link>
              </li>
            )}
          </ul>
        </div>
      </div>

      <Switch>
        {/* <Route path='/create'>
          {console.log('user: ',props.user !== null,fromCreate === true)}
          {fromCreate === true
            ? <BlogForm createBlog={createBlog} setFromCreate={setFromCreate} />
            : <Redirect to="/"/>
          }
        </Route> */}
        <Route path='/blogs/:id'>
          {/* <div style={showWhenVisible}> */}
          <Blog
            props={{
              ...props,
              // likeBlog: likeBlog,
              setVisible: setVisible,
              visible: visible,
              history: history
            }}
          />
        </Route>
      </Switch>
    </>
  )
}

function compareLikes(a, b) {
  return b.likes - a.likes
}

const mapStateToProps = (state) => {
  if ( state.filter === 'ALL' ) {
    return { blogs: state.blogs.sort(compareLikes) }
  } else if ( state.filter === 'SET_FILTER' ) {
    return {
      blogs: state.blogs.filter(blog => blog.title.toLowerCase()
        .includes(state.filter.content.toLowerCase())).sort(compareLikes)
    }
  }
  console.log('Blogs mapStateToProps: ',state)
  return { blogs: state.blog }
}

const mapDispatchToProps = {
  likeBlog,
  deleteBlog,
  setMessage
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Blogs)