import React from 'react'
import { useParams } from 'react-router-dom'
import { connect } from 'react-redux'
import { useDispatch } from 'react-redux'
import { likeBlog } from '../reducers/blogReducer'
import { deleteBlog } from '../reducers/blogReducer'
import { setMessage } from '../reducers/messageReducer'

const Blog = ({ props }) => {
  const id = useParams().id
  const dispatch = useDispatch()
  console.log('likeBlog1: ',likeBlog)
  console.log('likeBlog2: ',props.likeBlog)

  const blog = props.blogs.find(n => n.id === id)

  return(
    <div style={{ display: props.visible ? 'none' : '' }}>
      <br />
      <li className='list-group-item'>
        <b id='b-left'>{blog.title}</b>by<b id='b-right'>{blog.author}</b>
      </li>
      <br />
      <li className='list-group-item'>
        <a href={blog.url}>{blog.url}</a>
      </li>
      <li className='list-group-item'>
        {blog.likes} likes
        <button type='button' className='btn btn-success' id='like-button'
          onClick={() => {
            console.log('Like Clicked: ',id)
            props.likeBlog(blog)
            props.setMessage(`you liked '${blog.content}'`,5)
          }}>like</button>
      </li>
      <li className='list-group-item'>
        added by {blog.user.name}
        { props.user.name === blog.user.name &&
          <button type='button' id='delete-button' className='btn btn-warning'
            onClick={() => {
              console.log('Delete Clicked: ',id)
              props.deleteBlog(id)
              props.setMessage(`you deleted '${blog.content}'`,5)
            }}>delete</button>
        }
        <button type='button' id='back-button' className='btn btn-primary'
          onClick={() => {
            props.setVisible(true)
            props.history.goBack()
            // props.history.push(`/blogs/${blog.id}`)
          }} >back
        </button>
      </li>
    </div>
  )
}

// const mapStateToProps = (state) => {
//   console.log('Blog mapStateToProps state: ',state)
//   return { blog: state.blogs }
// }

const mapDispatchToProps = {
  likeBlog,
  deleteBlog,
  setMessage
}

export default connect(
  null,//mapStateToProps,
  mapDispatchToProps
)(Blog)
