let timeOutList = []

export const setMessage = (text, time) => {
  timeOutList.forEach(tout => typeof tout === 'number' ? clearTimeout(tout) : tout)
  return async dispatch => {
    await dispatch(messageChange(text))
    timeOutList.push(setTimeout(() => {
      dispatch(messageChange(null))
      timeOutList = []
    }, 1000*time))
  }
}

export const messageChange = (text) => {
  return {
    type: 'SET_MESSAGE',
    text
  }
}

export const setErrorMessage = (text, time) => {
  timeOutList.forEach(tout => typeof tout === 'number' ? clearTimeout(tout) : tout)
  return async dispatch => {
    await dispatch(messageChange(text))
    timeOutList.push(setTimeout(() => {
      dispatch(messageChange(null))
      timeOutList = []
    }, 1000*time))
  }
}

export const errorMessageChange = (text) => {
  return {
    type: 'SET_ERRORMESSAGE',
    text
  }
}

const messageReducer = (state = null, action) => {
  switch (action.type) {
  case 'SET_MESSAGE':
    return action.text
  case 'SET_ERRORMESSAGE':
    return action.text
  default:
    return state
  }
}

export default messageReducer